@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: local("Raleway"),
    local("Raleway Regular"),
    url("../fonts/ralewayRegular.woff2") format("woff2"),
    url("../fonts/ralewayRegular.woff") format("woff");
}

